import { Divider } from "antd";
import { Box } from "../core/MyBox";
import { UIFormatDateTimeVNComponent } from "../core/UI_FormatComponent";

export default function AboutItem(props: any) {
    return (
        <div className="my-box">
            <h4>{props.title}</h4>
            <Divider />
            <div>
                {props.data !== undefined ? props.data.map((data: any, index: number) => {
                    return (
                        <Box key={index}>
                            <strong>Version:</strong> {`${data.version.major}.${data.version.minor}.${data.version.patch}-${data.version.preRelease}`} - <strong>Release date: </strong>
                            <UIFormatDateTimeVNComponent value={data.dateRelease} />
                            <h3>{data.name}</h3>
                            <p>{data.message}</p>
                        </Box>
                    );
                }) : ""}
            </div>
        </div>
    );
}