import { useCallback, useEffect, useState } from 'react';
import { HomeService } from '../../services/homeService';
import { MyBox } from '../core/MyBox';
import AboutItem from './AboutItem';

export default function About() {
  const [dataSource, setDataSource] = useState<any>();

  const loadData = useCallback(() => {
    HomeService.GetListLogVersion()
      .then((rs: any) => {
        if (rs.isSuccess) {
          setDataSource({
            frontEnd: rs.data[0].data,
            backEnd: rs.data[1].data,
            database: rs.data[2].data,
          })
        }
      })
  }, [setDataSource]);

  useEffect(() => {
    loadData();
  }, [loadData]);

  return (
    <MyBox
      title="ThÃ´ng tin">
      <AboutItem title="Changelog Frontend" data={dataSource?.frontEnd} />
      <AboutItem title="Changelog Backend" data={dataSource?.backEnd} />
      <AboutItem title="Changelog Database" data={dataSource?.database} />
    </MyBox>
  );
}